
.recent-activity-title{
  margin-left: 8.5rem;
  margin-top: 6rem;
  margin-bottom: 1rem;
}
.recent-videos-container{
  margin-left: 7.5vw;
  width: 90%;
}
.video {  
  float: left;
  margin: 1vw;
  transition: ease-in-out 0.2s;
}
.video:hover {
  transform: scale(1.025);
  transition: ease-in-out 0.2s;
}
.video-thumbnail{
  position: relative;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 15vw;
  width: 20vw;
  height: 11vw;
  z-index: -1;
  opacity: 1;
}
.video-info-container{
  display: flex;
  width: 100%;
  align-items: center;
}
.video-channel-photo{
  width: 2vw;
  border-radius: 50%;
  padding: 0.25vw;
}
.video-titles-container{
  width: 16vw;
}
.video-duration{
  position: absolute;
  bottom: 0;
  right: 0;
  margin: 0.25vw;
  font-size: 1vw;
  color: white;
  background-color: #00000050;
  white-space: nowrap;
  padding: 0.25vw;
  padding-top: 0.1vw;
  padding-bottom: 0.1vw;
  border-radius:0.1vw;
  font-weight: 100;

}
.video-title{
  font-size: 1vw;
  font-weight: light;
  color: black;
}
.video-channel-title{
  font-size: 0.75vw;
  opacity: 0.5;
  color: black;
}

@media only screen and (max-width: 600px){
  .recent-activity-page{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .recent-activity-title{
    margin-left: 4rem;
  }
  .recent-videos-container{
    width: 90%;
    margin: 0;
    padding: 1rem;
    box-sizing: border-box;
  }
  .video{
    width: 100%;
    height: 17rem;
  }
  .video-thumbnail{
    width: 100%;
    background-size: 100% 15rem;
    height: 11rem;
  }

  .video-channel-photo{
    width: 20%;
  }
  .video-titles-container{
    width: 80%;
  }
  .video-title{
    width: 100%!important;
    font-size: 1rem;
  }
  .video-channel-title{
    font-size: 1rem;
  }
  .video-duration{
    padding: 0.25rem;
    padding-top: 0.1rem;
    padding-bottom: 0.1rem;
    font-size: 1rem;
    margin: 0.25rem;
  }
}