.profile-page{
    position: absolute;
    right: 0;
    width: calc(100% - 3.5rem);
    display: flex;
    flex-direction: column;
    align-items: center;
}
.profile-info-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20rem; 
}
.profile-info{
    margin-left: 2rem;
    position: relative;
}
.subscriber-count, .view-count, .video-count{
    font-size: 1.5rem;
    color: rgb(111,111,111);
    font-Weight: lighter;
    opacity: 0.5;
}
.subscriber-count{
    color: red;
}
.user-name{
    font-size: 3rem;
}
.profile-pic{
    box-shadow: 0 0.25rem 12px #00000020;
    width: 12rem;
    border-radius: 50%;
}
.youtube-link-icon{
    display: inline;
    position: absolute;
    top: 0;
    width: 1rem;
    transition: ease-in-out 0.2s;
    opacity: 0.5;
}
.youtube-link-icon:hover{
    cursor: pointer;
    transform: scale(1.2);
    transition: ease-in-out 0.2s;
    opacity: 0.8;
}
h2{
    font-size: 2rem;
    margin: 2rem;
    opacity: 0.7
}
.top-channels-title{
    margin: 0;
    padding: 0.25rem;
    margin-left: 2rem;
}
  .top-channels{
    box-shadow: 2px 5px 8px 2px #00000020;
    overflow: hidden;
    padding: 1%;
    width: 90vw;
    background-color: white;
    border-radius: 0.25rem;
  }
  .top-channels-container{
    margin: 0 auto;
    width: 95%;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-end;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .genres {
    box-shadow: 2px 5px 8px 2px #00000020;
    margin-top: 4em;
    margin-bottom: 4em;
    padding: 1rem;
    width: 90vw;
    background-color: white;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 0.25rem;
  }
  .genre-icon{
    border-radius: 50%;
    width: 100%;
    box-shadow: 0px 5px 8px 2px #00000020;
  }
  .genres-container{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
  .genres-title{
      margin: 0;
      margin-left: 2rem;
      padding: 0.5rem;
  }
  .genres hr, .top-channels hr{
    margin: 0 auto;
    width: 96%;
    opacity: 0.25;
}
/*channel style*/
.channel-container{
    padding-right: 1%;
    margin: 0.5%;
    margin-bottom: 3rem;
    display: flex;
    flex-direction: column;
    transition: ease-in-out 0.2s;
}
.channel-container:hover{
    transform: scale(1.05);
    transition: ease-in-out 0.2s;
    cursor: pointer;
}

@media only screen and (max-width: 600px) {
    .profile-page{
        width: 100%;
        overflow-x: hidden;
        margin-top: 5rem;
    }
    .profile-info ul{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 95vw;
        height: 2rem;
    }
    .profile-info li{

        margin-right: 1rem;
    }
    .profile-info-container{
        flex-direction: column;
        width: 100%;
    }
    .user-name{
        text-align: center;
        font-size: 2rem;
    }
    .channel-container{
        padding: 0.5rem;
        background-color: white;
        margin: 0;
        margin-bottom: 1rem;
        border-radius: 0.5rem;
        box-shadow: 0 0.25rem 12px #00000020;
    }
    .channel-container a{
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    .top-channels{
        width: 90%;
        box-shadow: none;
        background: none;
    }
    .top-channels-container{
        margin: 0;
        padding: 0;
        background: none;
        flex-direction: column;
        flex-wrap: nowrap;
        justify-content: flex-start;
        align-items: flex-start;
    }
    .genres{
        margin-left: 0.25rem;
        width: 85%;
    }
    .genres-container{
        flex-direction: column;
        align-items: center;
    }

    .genre{
        padding: 0.5rem;
        width: 100%!important; 
        flex-direction: row!important;
        align-items: center;

    }
    .genre-icon{
        width: 20%!important;
    }
    .genre-title{
        text-align: left!important;
        width: 80%;
        margin-left: 0.5rem;
        font-size: 1.5rem!important;
    }
  }