.top-bar{
    justify-content: flex-end;
    align-items: center;
    position: fixed;
    right: 0;
    top: 0;
    width: calc(100% - 4rem);
    height: 2.5rem;
    padding: 0.5rem;
    z-index: 3;
    background-color: white;
    border: 1px solid rgba(0, 0, 0, 0.1);
    box-shadow: 0px 0px 8px 2px #00000020;
}
.top-bar img, .top-bar button{
    margin-right: 1.5rem;
}
.top-bar-title{
    font-size: 1.25rem;
    font-weight: 700;
    transform: scaleY(1.1);
    margin: 0.5rem;
    margin-top: 0.25rem;
    opacity: 1;
}
.top-bar-logo{
    width: 2rem;
    padding: 0;
    margin: 0!important;
}
.grid-menu-icon, .notifications-icon{
    width: 1.25rem;
    height: 1.25rem;
    border: none;
    outline: none;
    background-color: transparent;
    margin: 0.5rem;
    opacity: 0.5;
    background-position: center;
    background-repeat: no-repeat;
    transition: all ease-in-out 0.2s;
}
.grid-menu-icon:hover, .notifications-icon:hover{
    opacity: 0.75;
    transition: all ease-in-out 0.2s;
    cursor: pointer;
}
.grid-menu-icon{
    background-image: url("../images/icons/grid-menu.svg");
}
.notifications-icon{
    background-image: url("../images/icons/notification.svg");
}
.account-button{
    width: 2rem;
    height: 2rem;
    border: none;
    outline: none;
    margin: 0.5rem;
    border-radius: 50%;
    cursor: pointer;
    background-image: url("../images/photos/mahadAhlie.jpg");
    background-size: cover;
    background-repeat: no-repeat;
}

@media only screen and (max-width: 600px) {
    .top-bar{
        width: 100%;
        padding: 0.25rem;
        position: fixed;
        top: 0;
        left: 0;
    }
    .account-button{
        width: 32px;
        height: 32px;
    }

    .notifications-icon, .grid-menu-icon{
        display: none;
    }

}