.channel-analytics-page{
    position: absolute;
    padding-left: 2rem;
    box-sizing: border-box;
    right: 0;
    width: calc(100% - 3.5rem);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.channel-analytics-title{
    margin-top: 6rem;
    margin-bottom: 2.5rem;
    font-size: 2.5rem;
    opacity: 0.7;
}
.message{
    padding-left: 2rem;
    font-size: 1.5rem;
    opacity: 0.5;
}