.sign-in-page{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.sign-in-background{
    width: 60vw;
    position: absolute;
    top: 5%;
    left: 0;
    z-index: -1;
    opacity: 0.07;
}
.sign-in-title{
    text-align:center;
    font-size: 4vw;
    margin-top: 10%;
}
.sign-in-button-container{
    width: 100%;
    margin-top: 2%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.app-description{
    text-align: center;
    margin-top: 1%;
    font-size: 1vw;
    opacity: 0.5;
}
.github-icon{
    width: 2vw;
    min-width: 2rem;
    margin-left: 2vw;
    transition: ease-in-out 0.3s;
    opacity: 0.6;
}
.footer {
    padding: 1.5rem;
    bottom: 0;
    position: absolute;
    text-align: center;
    opacity: 0.75;
    font-size: 0.85rem;
    font-weight: lighter;
  }
  @media only screen and (max-width: 770px) {
    .sign-in-title{
        font-size: 3rem;
    }
    .app-description{
        width: 95%;
        font-size: 0.95rem;
    }
    .app-description br{
        display: none;
    }
  }

  @media only screen and (max-width: 600px) {
    html, body {
        overflow-x: hidden;
      }
     body {
        width:100%;
      }
    .sign-in-page{
        padding: 1rem;
        box-sizing: border-box;
        width: 100vw;
        overflow: hidden;
    }
    .sign-in-background{
        width: 75vw;
        top: 10vh;
        left: -10%;
        opacity: 0.05;
    }
    .sign-in-title{
        margin-top: 20%;
        font-size: 2rem;
    }
    .sign-in-button-container{
        margin-top: 5%;
    }
    .app-description{
        width: 100%;
        font-size: 0.85rem;
        text-align: center;
        box-sizing: border-box;
    }

    .footer{
        font-size: 0.7rem;
        padding: 0.25rem;
        width: 100%;
    }
  }