.watch-statistics-page{
  width: calc(100% - 3.5rem);
  position: absolute;
  right: 0;
  padding: 2rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.watch-statistics-title{
  margin-left: 2rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  width: 100%;
}
.grid-container{

  display: grid;
  grid-template-rows: repeat(2,auto);
  grid-template-columns: repeat(2,auto);
  width: 44vw;
  height: 30vw;
}
.graph-grid{
  position: relative;
  width: 16vw;
  height: 11vw;
  display: flex;
  flex-direction: column;
}
.graph-flexbox{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.graph-container {
    border: 1px solid rgba(0, 0, 0, 0.1);
    margin: 1vw;
    padding: 1vw;
    padding-left: 2vw;
    padding-right: 2vw;
    margin-left: 0;
    background-color: white;
    border-radius: 0.15vw;
    box-shadow: 2px 5px 8px 2px #00000020;
  }
  
  svg {
    width: 35vw;
    height: 25vw;
    background: white;
    overflow: visible;
    margin-bottom: 2rem;
    display: block;
  }
  .tick line {
    visibility: hidden;
  }
  .bar-graph{
    width: 23vw;
    height: 26vw;
    display: inline-block;
  }
  .line-graph{
    width: 40vw;
    height: 23.5vw;
  }
  .donut-graph{
    width: 21vw;
    height: 23.5vw;
  }

  @media only screen and (max-width: 600px) {
    .watch-statistics-title{
      margin-left: 0;
      margin-right: 0;
      text-align: center;
    }
    .watch-statistics-page{
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100vw;
      overflow-y: scroll;
      overflow-x: hidden;
    }
    .graph-flexbox{
      flex-direction: column;
      align-items: flex-start;
      width: 100%;
      height: 100%;
    }
    .grid-container{
      width: 100%;
      height: auto;
      display: block;/****/
    }
    .graph-grid{
      display: flex;
      flex-direction: column;
      width: 100%;

      height: 20vh;
      margin-bottom: 1rem;
    }
    .graph-grid h3{
      font-size: 1.25rem!important;
    }
    .graph-grid strong{
      font-size: 2.25rem!important;
    }
    .graph-grid small{
      font-size: .75rem!important;
    }
    .bar-graph{
      width: 100%;
      height: 50vh;
      display: block;
    }
    .bar-graph h3,.line-graph h3, .donut-graph h3{
      font-size: 1rem;
    }
    .bar-graph span{
      font-size: 0.75rem!important;
    }
    .line-graph{
      width: 100%;
      height: 25vh;
    }
    .donut-graph{
      width: 100%;
      height: 50vh;
      margin-bottom: 2rem;

    }
    .donut-graph canvas{
      font-size: 0.5rem!important;
    }

    .bar-graph canvas, .line-graph canvas, .donut-graph canvas{
      width: 100%!important;
      height: 100%!important;
      font-size: 1rem;
    }
  }