.navbar {
    float: left;
    background-color: white;
    position: fixed;
    z-index: 4;
    top: 0;
    width: 3rem;
    overflow-x: hidden;
    overflow-y: hidden;
    height: 100%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    transition: ease-in-out 0.3s;
  }
  .rect{
    width: 3px;
    height: 3rem;
    background-color: #ff3d3d;
    opacity: 0.8;
    transition: all 0.2s;
    z-index: 5;
  }
  .menu-icon{
    border: none;
    background: none;
    z-index: 5;
    opacity: 0.6;
    padding: 1rem;
    padding-top: 1.5rem;
    width: 1.25rem;
    transition: ease-in-out 0.2s;
    cursor: pointer;
  }
  .menu-icon:hover{
    opacity: 0.6;
    transition: ease-in-out 0.2s;
  }
  .navbaricon {
    position: fixed;
    width: 1.5rem;
    padding: 0.75rem;

    opacity: .95;
    z-index: 5;
  }
  
  .navlink {
    overflow: hidden;
    margin-top: 0.5rem;
    width: 20rem;
    height: 5rem;
    font-size: 1.5rem;
    transition: ease-in-out 0.2s;
    opacity: .7;
    z-index: 5;
    display: flex;
    align-items: center;
  }
  .navlink:hover {
    opacity: 1;
    transition: ease-in-out 0.2s;
    cursor: pointer;
    color: red;
    transform: scale(1.01);
    transition: ease-in-out 0.2s;
  }
  .navlinktext {
    font-size: 1rem;
    margin-left: 4rem;
  }
  hr {
    opacity: .1;
  }
  .settings-button{
    position: absolute;
    bottom: 1rem;
    width: 1.5rem;
    opacity: 0.3;
    padding: 0.75rem;
    transition: ease-in-out 0.2s;
    
  }
  .settings-button:hover{
    opacity: 0.75;
    transition: ease-in-out 0.2s;
    transform: scale(1.1);
    cursor: pointer;
  }

  @media only screen and (max-width: 600px) {
    .navbar{
      width: 100vw;
      height: 3rem;
      box-sizing: border-box;
      padding-right: 2rem;
      float: none;
      top: auto;
      bottom: 0;
      left: 0;
      display: flex!important;
      justify-content: space-around;
      align-items: center;
      flex-direction: row;
    }
    .navbar a{
      position: default;
      width: 3rem;
    }
    .settings-button{
      padding: 0;
      margin: 0;
      position: default!important;
    }
    .navlink{ 
      width: 0;
    }
    .navbaricon, .navlink{
      margin: 0!important;
      padding: 0;
    }
    .rect, .box, .menu-icon, .navlinktext{
      display: none;
    }
  }